<template>
  <el-collapse-item :title="'模型' + index + ' - ' + algorithm" :name="index">
    <!-- <h3>{{ algorithm }}</h3> -->
    <h4>
      模型参数：
    </h4>
    <el-descriptions direction="vertical" border>
      <el-descriptions-item label="test_size">{{ model.train_algorithm_setting.test_size }}</el-descriptions-item>
      <el-descriptions-item v-for="para in model.train_algorithm_setting.paras[0]" label="learning_rate">{{ para
      }}</el-descriptions-item>
    </el-descriptions>
    <div class="model-result">
      <!-- 误差统计 -->
      <h4>
        BSR：
      </h4>
      <el-table v-if="Object.keys(result).length > 0" :data="[result.bsr.error]" style="width: 100%;margin-bottom: 30px;"
        stripe>
        <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
      <h4>
        SBR：
      </h4>
      <el-table v-if="Object.keys(result).length > 0" :data="[result.sbr.error]" style="width: 100%;margin-bottom: 30px;"
        stripe>
        <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
      <!-- 折线图 -->
      <div :id="'lr-bsr-lineplot_' + index"></div>
      <div :id="'lr-sbr-lineplot_' + index"></div>
      <div :id="'lr-d-lineplot_' + index"></div>
    </div>
  </el-collapse-item>
</template>

<script>
import { train } from "@/api";
import Plotly from 'plotly.js-dist-min';


export default {
  name: 'TrainSaveItem',
  props: ["index", "model"],
  data() {
    return {
      // 表头
      error_columns: ["MAE", "RMSE", "R2"],
    };
  },
  computed: {
    algorithm() {
      if (this.model.train_algorithm_setting.algorithm == 1) {
        return "最小二乘法线性回归";
      } else if (this.model.train_algorithm_setting.algorithm == 2) {
        return "随机森林回归";
      } else if (this.model.train_algorithm_setting.algorithm == 3) {
        return "BP神经网络回归";
      } else if (this.model.train_algorithm_setting.algorithm == 4) {
        return "XGBoost回归";
      } else if (this.model.train_algorithm_setting.algorithm == 5) {
        return "LightGBM回归";
      }
    },
    setting() {
      return this.model.train_algorithm_setting;
    },
    result() {
      return this.model.train_algorithm_result;
    },
  },
  methods: {
    // 绘制折线图
    draw_lineplot(id, tag) {
      // 直线图标题
      let title = "";
      if (tag == "bsr") {
        title = "BSR";
      }
      if (tag == "sbr") {
        title = "SBR";
      }
      if (tag == "d") {
        title = "D";
      }
      // 绘制折线图所需的数据
      const y_test = {
        x: Object.keys(this.result[tag].y_test),
        y: this.result[tag].y_test,
        type: 'lines+markers',
        name: "真实值"
      };
      const y_pred = {
        x: Object.keys(this.result[tag].y_pred),
        y: this.result[tag].y_pred,
        type: 'lines+markers',
        name: "预测值"
      };
      const data = [y_test, y_pred];
      const layout = {
        title
        // xaxis: {
        //   title: ''
        // }
      };
      Plotly.newPlot(id, data, layout);
    },
  },
  mounted() {
    // 绘制折线图
    if (Object.keys(this.result).length > 0) {
      this.draw_lineplot("lr-bsr-lineplot_" + this.index, "bsr");
      this.draw_lineplot("lr-sbr-lineplot_" + this.index, "sbr");
      this.draw_lineplot("lr-d-lineplot_" + this.index, "d");
    }
  }
};
</script>

<style lang="scss" scoped>
#train-save-item {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .setting-method {
      margin-bottom: 20px;
    }
  }

  // .model-tool {
  //   display: flex;
  //   flex-direction: row;
  //   justify-content: space-between;
  //   align-items: center;
  // }
}
</style>
