<template>
  <div id="process-result">
    <!-- 如果没有导入数据时提醒 -->
    <el-card v-if="!process_input_data.length > 0" class="warning" shadow="hover">
      <el-alert title="请先导入数据" type="warning" show-icon :closable="false">
      </el-alert>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <el-button type="primary" @click="process">点击处理</el-button>
      </div>
      <el-table v-if="process_result.length > 0" :data="process_result" style="width: 100%" stripe>
        <el-table-column v-for="column in columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import { process_user } from "@/api";

export default {
  name: 'ProcessResult',
  data() {
    return {
      // 表头
      columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"],
    };
  },
  computed: {
    process_input_data: { // 导入的数据
      get() {
        return this.$store.state.process_input_data;
      },
      set(newVal) {
        this.$store.commit('set_process_input_data', newVal);
      }
    },
    process_setting: { // 处理方法参数选择
      get() {
        return this.$store.state.process_setting;
      },
      set(newVal) {
        this.$store.commit('set_process_setting', newVal);
      }
    },
    process_result: { // 处理后的数据
      get() {
        return this.$store.state.process_result;
      },
      set(newVal) {
        this.$store.commit('set_process_result', newVal);
      }
    },
  },
  watch: {
    // 实现深度改变时更新process_setting
    process_setting: {
      handler(newVal, oldVal) {
        this.process_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 发送请求处理数据
    process() {
      // 检查是否导入数据
      if (this.process_input_data.length > 0) {
        // 添加导入的数据
        this.process_setting.data = this.process_input_data;
        process_user(this.process_setting).then(result => {
          this.process_result = JSON.parse(result);
        });
      } else {
        this.$message({
          showClose: true,
          message: '请先导入数据！',
          type: 'error'
        });
      }
    }
  },
  mounted() {
  }
};
</script>

<style lang="scss" scoped>
#process-result {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
  }
}
</style>