<template>
  <div id="train-algorithm-lgb">
    <el-card shadow="hover">
      <div slot="header">
        <b>参数设置</b>
      </div>
      <div class="setting">
        <!-- learning_rate -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>learning_rate：</span>
          <el-input-number v-model="train_algorithm_lgb_setting.paras[0].learning_rate" :min="0.1"
            :step="0.1"></el-input-number>
        </div>
        <!-- random_state -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>random_state：</span>
          <el-input-number v-model="train_algorithm_lgb_setting.paras[0].random_state" :min="0"
            :step="0.01"></el-input-number>
        </div>
        <!-- max_depth -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>max_depth：</span>
          <el-input-number v-model="train_algorithm_lgb_setting.paras[0].max_depth" :min="1" :step="1"></el-input-number>
        </div>
        <!-- n_estimators -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>n_estimators：</span>
          <el-input-number v-model="train_algorithm_lgb_setting.paras[0].n_estimators" :min="1"
            :step="1"></el-input-number>
        </div>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>参数说明</b>
      </div>
      <el-descriptions title="" direction="vertical" border>
        <el-descriptions-item label="learning_rate">learning_rate</el-descriptions-item>
        <el-descriptions-item label="random_state">learning_rate</el-descriptions-item>
        <el-descriptions-item label="max_depth">learning_rate</el-descriptions-item>
        <el-descriptions-item label="n_estimators">learning_rate</el-descriptions-item>
      </el-descriptions>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>算法说明</b>
      </div>
      算法说明
    </el-card>
    <el-card class="form-upload" shadow="hover">
      <div slot="header">
        <div class="model-tool">
          <el-button type="primary" @click="train_model">训练模型</el-button>
          <el-button type="primary" @click="save">保存模型</el-button>
        </div>
      </div>
      <div v-show="Object.keys(train_algorithm_lgb_result).length > 0" class="model-result">
        <!-- 误差统计 -->
        <b>
          BSR：
        </b>
        <el-table v-if="Object.keys(train_algorithm_lgb_result).length > 0" :data="[train_algorithm_lgb_result.bsr.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <b>
          SBR：
        </b>
        <el-table v-if="Object.keys(train_algorithm_lgb_result).length > 0" :data="[train_algorithm_lgb_result.sbr.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <!-- 折线图 -->
        <div id="lr-bsr-lineplot"></div>
        <div id="lr-sbr-lineplot"></div>
        <div id="lr-d-lineplot"></div>
      </div>
    </el-card>
  </div>
</template>

<script>
// import * as XLSX from 'xlsx';
import { train } from "@/api";
import Plotly from 'plotly.js-dist-min';


export default {
  name: 'TrainAlgorithmLgb',
  data() {
    return {
      // 表头
      error_columns: ["MAE", "RMSE", "R2"],
    };
  },
  computed: {
    train_process_result: { // 处理后的内置数据
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
    train_algorithm_lgb_setting: { // LightGBM回归参数
      get() {
        return this.$store.state.train_algorithm_lgb_setting;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_lgb_setting', newVal);
      }
    },
    train_algorithm_lgb_result: { // LightGBM回归训练结果
      get() {
        return this.$store.state.train_algorithm_lgb_result;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_lgb_result', newVal);
      }
    },
    train_save: { // 保存的模型
      get() {
        return this.$store.state.train_save;
      },
      set(newVal) {
        this.$store.commit('set_train_save', newVal);
      }
    },
  },
  watch: {
    // 实现深度改变时更新train_algorithm_lgb_setting
    train_algorithm_lgb_setting: {
      handler(newVal, oldVal) {
        this.train_algorithm_lgb_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 训练模型
    train_model() {
      // 没有处理过数据时警告
      if (this.train_process_result.length > 0) {
        // 发送请求
        this.train_algorithm_lgb_setting.data = this.train_process_result;
        train(this.train_algorithm_lgb_setting).then(result => {
          this.train_algorithm_lgb_result = result;
          // 绘制折线图
          this.draw_lineplot("lr-bsr-lineplot", "bsr");
          this.draw_lineplot("lr-sbr-lineplot", "sbr");
          this.draw_lineplot("lr-d-lineplot", "d");
        });
      } else {
        this.$message({
          showClose: true,
          message: '请先处理内置数据！',
          type: 'error'
        });
      }
    },
    // 保存训练的模型
    save() {
      // 没有训练过即没有可以保存的模型时警告
      if (Object.keys(this.train_algorithm_lgb_result).length > 0) {
        const train_algorithm_lgb_setting = this.train_algorithm_lgb_setting;
        const train_algorithm_lgb_result = this.train_algorithm_lgb_result;
        this.train_save = {
          train_algorithm_setting: train_algorithm_lgb_setting,
          train_algorithm_result: train_algorithm_lgb_result
        };
        // 保存成功通知
        this.$message({
          message: '保存模型成功',
          type: 'success'
        });
      } else {
        this.$message({
          showClose: true,
          message: '请先训练模型！',
          type: 'error'
        });
      }
    },
    // 绘制折线图
    draw_lineplot(id, tag) {
      // 直线图标题
      let title = "";
      if (tag == "bsr") {
        title = "BSR";
      }
      if (tag == "sbr") {
        title = "SBR";
      }
      if (tag == "d") {
        title = "D";
      }
      // 绘制折线图所需的数据
      const y_test = {
        x: Object.keys(this.train_algorithm_lgb_result[tag].y_test),
        y: this.train_algorithm_lgb_result[tag].y_test,
        type: 'lines+markers',
        name: "真实值"
      };
      const y_pred = {
        x: Object.keys(this.train_algorithm_lgb_result[tag].y_pred),
        y: this.train_algorithm_lgb_result[tag].y_pred,
        type: 'lines+markers',
        name: "预测值"
      };
      const data = [y_test, y_pred];
      const layout = {
        title
        // xaxis: {
        //   title: ''
        // }
      };
      Plotly.newPlot(id, data, layout);
    },
  },
  mounted() {
    // 绘制折线图
    if (Object.keys(this.train_algorithm_lgb_result).length > 0) {
      this.draw_lineplot("lr-bsr-lineplot", "bsr");
      this.draw_lineplot("lr-sbr-lineplot", "sbr");
      this.draw_lineplot("lr-d-lineplot", "d");
    }
  }
};
</script>

<style lang="scss" scoped>
#train-algorithm-lgb {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .setting-method {
      margin-bottom: 20px;
    }
  }

  // .model-tool {
  //   display: flex;
  //   flex-direction: row;
  //   justify-content: space-between;
  //   align-items: center;
  // }
}
</style>
