<template>
  <div id="process-input">
    <el-card class="setting" shadow="hover">
      <div class="setting-method" style="margin-right: 20px;">
        <span>选择导入数据方法：</span>
        <el-select v-model="process_input_method" placeholder="导入数据方法">
          <el-option v-for="method in process_input_methods" :key="method.value" :label="method.label"
            :value="method.value">
          </el-option>
        </el-select>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>参数说明</b>
      </div>
      <el-table :data="process_info_form" stripe>
        <el-table-column v-for="column in process_form_columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
    </el-card>
    <el-card v-if="process_input_method == 1" class="form-example" shadow="hover">
      <div slot="header">
        <b>
          上传表格示例
        </b>
      </div>
      <el-table :data="process_example_form" style="width: 100%" stripe>
        <el-table-column v-for="column in process_form_columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
    </el-card>
    <el-card v-if="process_input_method == 1" class="form-upload" shadow="hover">
      <div slot="header">
        <el-upload class="upload-file" ref="upload" action="/" :show-file-list="false" :on-change="upload_excel"
          accept=".csv,.xlsx,.xls" :auto-upload="false">
          <el-button icon="el-icon-upload" size="small" type="primary">
            上传表格文件
          </el-button>
        </el-upload>
      </div>
      <el-table v-if="process_input_data.length > 0" :data="process_input_data" style="width: 100%" stripe>
        <el-table-column v-for="column in process_form_columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
    </el-card>
    <el-card v-if="process_input_method == 2" class="form-online" shadow="hover">
      online
    </el-card>
  </div>
</template>

<script>
import * as XLSX from 'xlsx';

export default {
  name: 'ProcessInput',
  data() {
    return {
      // 选择导入数据方法
      process_input_methods: [{
        value: 1,
        label: '上传表格文件'
      }, {
        value: 2,
        label: '在线输入'
      }],
      // 表头
      process_form_columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"],
      // 参数说明
      process_info_form: [
        { // 参数含义
          "SPO": "砂层孔隙度",
          "BMPO": "潜山基质孔隙度",
          "BFPO": "潜山裂缝孔隙度",
          "SPE": "砂岩渗透率",
          "BMPE": "潜山基质渗透率",
          "BFPE": "潜山裂缝渗透率",
          "ST": "砂岩厚度",
          "BT": "潜山厚度",
          "WP": "井间距",
          "FN": "裂缝数",
          "SFR": "砂层流速",
          "BFR": "潜山层流速",
        },
        { //参数单位
          "SPO": "无量纲",
          "BMPO": "无量纲",
          "BFPO": "无量纲",
          "SPE": "平方米",
          "BMPE": "平方米",
          "BFPE": "平方米",
          "ST": "米",
          "BT": "米",
          "WP": "米",
          "FN": "无量纲",
          "SFR": "米每秒",
          "BFR": "米每秒",
        }
      ],
      // 上传表格示例
      process_example_form: [
        {
          "SPO": "0.1",
          "BMPO": "0.078",
          "BFPO": "1",
          "SPE": "2.50E-13",
          "BMPE": "2.31E-15",
          "BFPE": "1.15E-10",
          "ST": "5.00E+01",
          "BT": "1.40E+02",
          "WP": "2.50E+02",
          "FN": "1.50E+02",
          "SFR": "5.00E-06",
          "BFR": "5.00E-06",
        },
        {
          "SPO": "0.15",
          "BMPO": "0.078",
          "BFPO": "1",
          "SPE": "2.50E-13",
          "BMPE": "2.31E-15",
          "BFPE": "1.15E-10",
          "ST": "5.00E+01",
          "BT": "1.40E+02",
          "WP": "2.50E+02",
          "FN": "1.50E+02",
          "SFR": "5.00E-06",
          "BFR": "5.00E-06",
        },
        {
          "SPO": "0.2",
          "BMPO": "0.078",
          "BFPO": "1",
          "SPE": "2.50E-13",
          "BMPE": "2.31E-15",
          "BFPE": "1.15E-10",
          "ST": "5.00E+01",
          "BT": "1.40E+02",
          "WP": "2.50E+02",
          "FN": "1.50E+02",
          "SFR": "5.00E-06",
          "BFR": "5.00E-06",
        },
        {
          "SPO": "0.25",
          "BMPO": "0.078",
          "BFPO": "1",
          "SPE": "2.50E-13",
          "BMPE": "2.31E-15",
          "BFPE": "1.15E-10",
          "ST": "5.00E+01",
          "BT": "1.40E+02",
          "WP": "2.50E+02",
          "FN": "1.50E+02",
          "SFR": "5.00E-06",
          "BFR": "5.00E-06",
        },
        {
          "SPO": "0.3",
          "BMPO": "0.078",
          "BFPO": "1",
          "SPE": "2.50E-13",
          "BMPE": "2.31E-15",
          "BFPE": "1.15E-10",
          "ST": "5.00E+01",
          "BT": "1.40E+02",
          "WP": "2.50E+02",
          "FN": "1.50E+02",
          "SFR": "5.00E-06",
          "BFR": "5.00E-06",
        }
      ]
    };
  },
  computed: {
    process_input_method: { // 导入数据的方式
      get() {
        return this.$store.state.process_input_method;
      },
      set(newVal) {
        this.$store.commit('set_process_input_method', newVal);
      }
    },
    process_input_data: { // 导入的数据
      get() {
        return this.$store.state.process_input_data;
      },
      set(newVal) {
        this.$store.commit('set_process_input_data', newVal);
      }
    },
  },
  methods: {
    // 上传表格文件
    upload_excel(file) {
      new Promise(function (resolve, reject) {
        const reader = new FileReader();
        reader.onload = function (e) {
          const data = e.target.result;
          this.wb = XLSX.read(data, {
            type: 'binary'
          });
          const result = [];
          this.wb.SheetNames.forEach((sheetName) => {
            result.push({
              sheetName: sheetName,
              columns: XLSX.utils.sheet_to_json(this.wb.Sheets[sheetName], { header: 1 })[0],
              sheet: XLSX.utils.sheet_to_json(this.wb.Sheets[sheetName]),
            });
          });
          resolve(result);
        };
        reader.readAsBinaryString(file.raw);
      }).then(tabJson => {
        if (tabJson && tabJson.length > 0) {
          // 保存到store
          // this.process_upload_sheetname = tabJson[0].sheetName; // sheetname
          // this.process_upload_columns = tabJson[0].columns; // 表头
          this.process_input_data = tabJson[0].sheet; //表格数据
        }
      });
    },
  }
};
</script>

<style lang="scss" scoped>
#process-input {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }
}
</style>
